*{margin:0; padding: 0;}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.hl_images{
  height: 50px;
 
}

.header{
  padding: 5px;
  display: flex;
  justify-content: space-between;
  position: sticky;
  top: 0;
  background-color: #fff;
  z-index: 100;
  box-shadow:0px 5px 8px -9px rgba(0,0,0,0.75);
  
}
.header_left{
  display: flex;
  justify-content: space-evenly;
  align-items:center;
}

.h_input{
  display: flex;
  align-items:center ;
  background-color:#F0F0F0;
  padding: 10px;
  margin-left: 10px;
  border-radius: 999px;
}
.hi_imput{
  border: none;
  background-color: transparent;
  outline-width:0;
  
}

.header_middle{
  display: flex;
  justify-content: center;
  flex: 1;
  
}
.hm_icons>.MuiSvgIcon-root{
  color: grey;
  
}

.hm_icons:hover >.MuiSvgIcon-root{
  color:#2e81f4;
}

.hm_icons{
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 0 30px;
}

.hm_icons:hover{
  border-radius:15px; 
  background-color: #F0F0F0;
  border-bottom:none;
  padding:0 30px;
}

.header_right{
  display: flex;
  align-items: center;
}
.h_info{
  display: flex;
  align-items: center;
  padding-right: 20px;
}

.h_info>h4{
  padding-left:10px ;
}

.sidebarRow{
  display :flex;
  padding: 10px;
  cursor: pointer;
  align-items: center;

}
.sidebarRow:hover{
  border-radius:10px;
  background-color: #fff;
}

.sidebarRow>.MuiSvgIcon-root{
  font-size: xx-large;
  color: #2e81f4;
}
.sbr_h{
  margin-left: 20px;
  font-weight: 600;
}
.sidebar{
  background-color:lightgray;
}

